import React from 'react';
import { Link } from 'react-router-dom';
import './index.less';

const routeList = [
  { path: '/', label: '' },
  { path: '/getstart', label: '' },
  { path: '/api', label: 'API' },
  { path: '/about', label: '' },
  { path: '/login', label: '' },
];

const Header = () => (
  <div className="header-wrapper">
    <a className="brand">
      <img
        src="//o4j806krb.qnssl.com/public/images/cnodejs_light.svg"
        alt="cnode"
      />
    </a>
    <ul className="nav pull-right">
      {routeList.map(route => {
        return (
          <li key={route.path}>
            <Link to={route.path}>
              {route.label}
            </Link>
          </li>
        );
      })}
    </ul>
  </div>
);

export default Header;



